
import React, { Component } from 'react'
import { HashRouter as Router,NavLink, Redirect, Route,Switch} from 'react-router-dom'
import routes from '../router.config';
import './app.css'

function RouteWithSubRoutes(route) {
    if (route.pri){
        return (
         <Route path ={route.path}
         pri ={route.pri}
         render={()=>{
            return <PrivateRoute {...route} routes={route.routes}/>
    }}
         />
        )
    }
    return (
      <Route
        path={route.path}
        pri ={route.pri}
        render={props => {
          // pass the sub-routes down to keep nesting
          return <route.component {...props} routes={route.routes} />
        }}
      />
    );
  }
function PrivateRoute({component:Com,routes:routes,...rest}){
    return (
        <Route {...rest}
            render={({location})=>{
                return localStorage.getItem('userinfo')?
                <Com routes={routes}/>:
                (<Redirect to={{pathname:'/login',state:location}}/>)
            }
        }
        />
    );
}

class App extends Component {
    render() {
        return (
            <Router>
                <header>
                    <nav>
                        <NavLink activeClassName='active' exact to='/'>React</NavLink>
                        <NavLink activeClassName='active' to='/doc'>文档</NavLink>
                        <NavLink activeClassName='active' to='/tuto'>教程</NavLink>
                        <NavLink activeClassName='active' to='/blog'>博客</NavLink>
                        <NavLink activeClassName='active' to='/comm'>社区</NavLink>
                    </nav>
                </header>
                <Switch>
                    {
                        routes.map((route,i)=>{
                            return <RouteWithSubRoutes key={i} {...route} />
                            
                        })
                    }
                </Switch>
            </Router>
        )
    }
}
export default App;
export {PrivateRoute,RouteWithSubRoutes};
