
import React from 'react'
import { NavLink,HashRouter as Route,Switch,Redirect} from 'react-router-dom'

import {RouteWithSubRoutes} from './App'
import './doc.css'
const Doccontent = (props)=>{
    return <div>
        <h1>{props.match.params.type}</h1>
    </div>
}

const Doc = (props) => {
    let routess = props.routes;
    return (
        <div className='doc'>
            <div className="content">
                <Switch>
                    {
                        routess.map((routes0,i)=>{
                            return <RouteWithSubRoutes key={i} {...routes0}/>
                        }
                        
                        )
                    }
                </Switch>
                <Redirect from='/doc' to='/doc/core'/>
            </div>
            <ol>
                <li><NavLink to='/doc/core' exact>核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>高级引导</NavLink></li>
                <li><NavLink to='/doc/api'>API</NavLink></li>
                <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
            </ol>
        </div>
    )
}

export default Doc

